.rightBar {
    flex: 3;
    position: sticky;
    top: 70px;
    height: calc(100vh - 70px);
    overflow: scroll;
    &::-webkit-scrollbar {
        display: none;
    }
    .container {
        padding: 20px;
        .item {
            -webkit-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
            -moz-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
            box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
            ;
            padding: 20px;
            margin-bottom: 20px;
            span {
                color: gray;
            }
            .user {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 20px 0px;
                .userInfo {
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    position: relative;
                    img {
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                    .online {
                        width: 12px;
                        height: 12px;
                        border-radius: 50%;
                        background-color: limegreen;
                        position: absolute;
                        top: 0px;
                        left: 30px;
                    }
                    p {
                        color: gray;
                    }
                    span {
                        font-weight: 600;
                        color: black;
                    }
                }
                .buttons {
                    display: flex;
                    align-items: center;
                    gap: 10px;
                }
                button {
                    padding: 5px;
                    border: none;
                    color: white;
                    cursor: pointer;
                    &:first-child {
                        background-color: #5271ff;
                    }
                    &:last-child {
                        background-color: #f0544f;
                    }
                }
            }
        }
    }
}